@import "../vars.scss";
.prize-card {
    border: 1px solid #E0E0E0;
    border-radius: 8px;
    overflow: hidden;
    &__top {
        display: flex;
        align-items: center;
        justify-content: center;
        padding: 6px;
    }
    &__img {
        border: 2px solid #E0E0E0;
        width: 141px;
        height: 80px;
        position: relative;
        border-radius: 50%;
        margin-right: 23px;
        flex-shrink: 0;
        img {
            position: absolute;
            left: 50%;
            top: 50%;
            transform: translate(-50%, -50%);
        }
    }
    &__bottom {
        display: flex;
        align-items: center;
        padding: 13px;
        background-color: $blue;
        width: calc(100% + 2px);
        margin-left: -1px;
        margin-bottom: -1px;
    }
    &__country {
        font-weight: 500;
        font-size: 14px;
        line-height: 120%;
        text-transform: uppercase;
        padding-left: 6px;
        display: flex;
        align-items: center;
        img {
            margin-right: 10px;
        }
    }
    &__sum {
        font-weight: 700;
        font-size: 26px;
        line-height: 22px;
        color: $green;
        margin-top: 12px;
    }
    &__time {
        display: flex;
        align-items: center;
        margin-left: 40px;
        font-weight: 500;
        font-size: 18px;
        color: #FFFFFF;
        img {
            margin-right: 4px;
            flex-shrink: 0;
        }
    }
}

.lottery-card {
    border: 1px solid #E0E0E0;
    border-radius: 8px;
    padding: 13px;
    position: relative;
    &__favourite {
        position: absolute;
        top: 10px;
        right: 18px;
    }
    &__img {
        width: 95px;
        height: 95px;
        display: flex;
        align-items: center;
        justify-content: center;
        border: 2px solid #E0E0E0;
        margin: 0 auto;
        border-radius: 50%;
        img {
            max-width: 100%;
            max-height: 100%;
        }
    }
    &__country {
        font-weight: 500;
        font-size: 14px;
        line-height: 120%;
        text-transform: uppercase;
        margin-top: 28px;
        justify-content: center;
        display: flex;
        align-items: center;
        img {
            margin-right: 10px;
        }
    }
    &__sum {
        font-weight: 700;
        font-size: 20px;
        line-height: 22px;
        text-align: center;
        margin-top: 8px;
    }
    &__time {
        display: flex;
        align-items: center;
        justify-content: center;
        font-weight: 500;
        font-size: 16px;
        color: $blue;
        margin-top: 8px;
        img {
            margin-right: 4px;
            flex-shrink: 0;
        }
    }
    &__btn {
        width: 100%;
        margin-top: 12px;
    }
}

.win-card {
    border: 1px solid #E0E0E0;
    border-radius: 8px;
    padding: 10px 15px;
    display: flex;
    &__img {
        flex-shrink: 0;
        width: 75px;
        margin-right: 15px;
        img {
            width: 100%;
        }
    }
    &__date,
    &__country,
    &__desc {
        font-size: 14px;
        line-height: 21px;
    }
    &__country {
        font-weight: 500;
        margin-top: -5px;
        img {
            margin-right: 5px;
        }
    }
    &__desc {
        color: $grey;
    }
}

@media screen and (max-width: 576px) {
    .prize-card {
        &__img {
            width: 128px;
            height: 80px;
            margin-right: 20px;
            img {
                max-height: 100%;
            }
        }
        &__country {
            padding-left: 0;
        }
        &__btn {
            width: 144px;
        }
        &__time {
            margin-left: 49px;
        }
        &__sum {
            font-size: 24px;
            line-height: 22px;
        }
    }
    .lottery-card {
        padding: 16px 8px 10px;
        &__favourite {
            top: 8px;
            right: 8px;
        }
        &__img {
            width: 80px;
            height: 80px;
        }
        &__country {
            font-size: 12px;
            margin-top: 12px;
            img {
                margin-right: 5px;
            }
        }
        &__sum {
            font-size: 18px;
        }
        &__time {
            font-size: 14px;
            line-height: 18px;
        }
        &__btn {
            margin-top: 8px;
        }
    }
}

@media screen and (max-width: 375px) {
    .prize-card {
        &__img {
            margin-right: 15px;
        }
        &__country {
            font-size: 13px;
            img {
                margin-right: 5px;
            }
        }
        &__sum {
            font-size: 20px;
            line-height: 18px;
        }
        &__time {
            margin-left: 30px;
        }
    }
}