@import "vars";
.footer {
    background: linear-gradient(0deg, rgba(224, 224, 224, 0.47) 0%, rgba(225, 225, 225, 0.16) 20.75%, rgba(255, 254, 254, 0.26) 84.14%, #FFFFFF 126.52%);
    background-size: 1200px auto, cover;
    padding: 103px 0 55px;
    margin-top: 33px;
    position: relative;
    &:before {
        content: '';
        position: absolute;
        z-index: -1;
        width: 100%;
        height: 100%;
        background-image: url(../img/footer-bg.webp), ;
        background-repeat: no-repeat;
        background-position: center top;
        opacity: 0.86;
        left: 0;
        top: 0;
    }
    &__inner {
        padding: 0 110px;
    }
    &__top {
        display: flex;
        align-items: center;
        justify-content: space-between;
    }
    &__whatsapp {
        display: flex;
        align-items: center;
        img {
            width: 36px;
            margin-right: 15px;
            flex-shrink: 0;
        }
        &-title {
            font-weight: 500;
            font-size: 16px;
            line-height: 21px;
        }
        &-desc {
            font-size: 12px;
            line-height: 21px;
            color: $grey;
        }
    }
    &__socials {
        display: flex;
        align-items: center;
        width: 155px;
        &-item {
            img {
                width: 36px;
            }
            &:hover {
                transform: scale(1.1);
            }
            &:not(:last-child) {
                margin-right: 10px;
            }
        }
    }
    &__bottom {
        display: flex;
        justify-content: space-between;
        margin-top: 36px;
    }
    &__title {
        font-weight: 500;
        font-size: 16px;
        line-height: 21px;
        text-transform: uppercase;
        margin-bottom: 15px;
    }
    &__link {
        display: block;
        font-size: 16px;
        line-height: 21px;
        margin-bottom: 5px;
        strong {
            font-weight: 700;
        }
        &:hover {
            color: $blue;
        }
    }
    &__col {
        &:nth-child(2) {
            max-width: 250px;
        }
        &:nth-child(3) {
            width: 155px;
        }
    }
    &__desc {
        font-size: 12px;
        line-height: 120%;
    }
    &__info {
        font-size: 13px;
        line-height: 21px;
        margin-top: 6px;
    }
    &__pays {
        display: grid;
        grid-template-columns: repeat(3, 1fr);
        gap: 10px;
        img {
            width: 100%;
            display: block;
        }
    }
    &__security {
        display: flex;
        img {
            display: block;
            &:not(:last-child) {
                margin-right: 10px;
            }
        }
        &-block {
            margin-top: 30px;
        }
    }
}

@media screen and (max-width: 1200px) {
    .footer {
        &::before {
            background-size: 100% autos;
        }
        &__inner {
            padding: 0;
        }
    }
}

@media screen and (max-width: 992px) {
    .footer {
        padding: 80px 0 32px;
        margin-top: 15px;
        &__whatsapp {
            img {
                width: 30px;
            }
            &-title {
                font-weight: 500;
                font-size: 14px;
                line-height: 120%;
            }
            &-desc {
                line-height: 120%;
            }
        }
        &__socials {
            width: unset;
            &-item {
                img {
                    width: 30px;
                }
            }
        }
        &__bottom {
            display: block;
        }
        &__col {
            &:not(:last-child) {
                margin-bottom: 20px;
            }
            &:nth-child(2) {
                max-width: 100%;
            }
            &:nth-child(3) {
                display: flex;
                width: unset;
            }
        }
        &__links {
            display: grid;
            grid-template-columns: 1fr 1fr;
            gap: 5px 16px;
        }
        &__link {
            margin: 0;
        }
        &__title {
            margin-bottom: 12px;
        }
        &__security {
            &-block {
                margin: 0 0 0 34px;
            }
        }
    }
}

@media screen and (max-width: 576px) {
    .footer {
        &__security {
            &-block {
                padding-bottom: 42px;
            }
        }
        &__inner {
            position: relative;
        }
        &__socials {
            position: absolute;
            bottom: 0;
            left: 190px;
        }
        &__title,
        &__link {
            font-size: 14px;
        }
    }
}

@media screen and (max-width: 370px) {
    .footer {
        &__col {
            &:nth-child(3) {
                display: block;
            }
        }
        &__security {
            &-block {
                margin: 20px 0 0;
            }
        }
        &__pays {
            &-block {
                max-width: 160px;
            }
        }
        &__socials {
            left: 0;
        }
        &__links {
            grid-template-columns: 1fr;
        }
    }
}