@import "../vars";
.blue-link {
    font-weight: 500;
    font-size: 18px;
    line-height: 24px;
    text-decoration-line: underline;
    color: $blue;
    &:hover {
        color: $black;
    }
}

.display2 {
    font-weight: 500;
    font-size: 24px;
    line-height: 150%;
    text-transform: uppercase;
}

@media screen and (max-width: 768px) {
    .blue-link {
        font-size: 16px;
    }
    .display2 {
        font-size: 18px;
        line-height: 133%;
    }
}