@import "../vars";
.btn {
    display: flex;
    align-items: center;
    justify-content: center;
    transition: all ease-out .225s;
    padding: 0;
    &:focus {
        box-shadow: none;
    }
}

.btn-yellow {
    width: 171px;
    height: 38px;
    background-color: $yellow;
    border-radius: 8px;
    font-weight: 500;
    font-size: 16px;
    color: $black;
    &:hover {
        color: $black;
        background-color: lighten($yellow, 20%);
    }
}