@import "vars";
.header {
    padding: 15px 0 30px;
    &__logo {
        width: 280px;
        display: block;
        margin-right: 30px;
        img {
            width: 100%;
        }
    }
    &__info {
        &-item {
            font-size: 14px;
            line-height: 120%;
            strong {
                font-weight: 700;
                line-height: 130%;
            }
            a {
                line-height: 130%;
                font-weight: 500;
                color: $blue;
                &:hover {
                    opacity: 0.8;
                }
            }
        }
    }
    &__socials {
        display: flex;
        align-items: center;
        &-item {
            &:hover {
                transform: scale(1.1);
            }
            &:not(:last-child) {
                margin-right: 10px;
            }
        }
    }
    &__lang {
        margin-top: 10px;
        &-show {
            display: flex;
            align-items: center;
            font-size: 14px;
            line-height: 150%;
            span {
                margin: 0 5px;
            }
        }
    }
    &__links {
        display: flex;
        align-items: center;
        flex-grow: 1;
    }
    &__link {
        height: 34px;
        display: flex;
        align-items: center;
        padding: 0 25px;
        font-weight: 700;
        font-size: 16px;
        line-height: 120%;
        text-transform: uppercase;
        color: #FFFFFF;
        position: relative;
        &--active {
            text-decoration: underline;
            pointer-events: unset;
        }
        &:hover {
            color: rgba(#fff, 0.8);
        }
        &:before {
            content: '';
            position: absolute;
            z-index: 1;
            right: 0;
            top: 50%;
            transform: translateY(-50%);
            height: calc(100% - 10px);
            width: 1px;
            background-color: #fff;
        }
    }
    &__account {
        padding: 0 38px;
        height: 34px;
        display: flex;
        align-items: center;
        font-weight: 700;
        font-size: 16px;
        line-height: 120%;
        text-transform: uppercase;
        color: #FFFFFF;
        background-color: $blue;
        border-radius: 8px;
        flex-shrink: 0;
        &:hover {
            color: rgba(#fff, 0.8);
        }
    }
    &__burger,
    &__profile {
        display: none;
    }
}

.header-top {
    &__inner {
        display: flex;
        align-items: center;
    }
    &__right {
        margin-left: auto;
    }
}

.header-bottom {
    margin-top: 15px;
    &__inner {
        display: flex;
        align-items: center;
        border-radius: 8px;
        background-color: $red;
    }
}

@media screen and (max-width: 992px) {
    .header {
        padding: 16px 0 26px;
        &__info {
            width: 100%;
            order: 2;
            display: flex;
            align-items: center;
            justify-content: space-between;
            margin-top: 6px;
        }
        &__burger {
            background-image: url(../img/menu.svg);
            width: 24px;
            height: 24px;
            display: block;
            background-repeat: no-repeat;
            background-position: center;
        }
        &__profile {
            width: 24px;
            height: 24px;
            display: flex;
            align-items: center;
            justify-content: center;
            background-color: $blue;
            border-radius: 4px;
        }
        &__logo {
            width: 182px;
            margin: 0;
        }
    }
    .header-top {
        &__inner {
            flex-wrap: wrap;
            justify-content: space-between;
        }
        &__right {
            display: none;
        }
    }
    .header-bottom {
        display: none;
    }
}