@import "vars";
.main {
    &__banner {
        display: block;
        img {
            width: 100%;
            border-radius: 8px;
            display: block;
        }
    }
    &__items {
        display: grid;
        grid-template-columns: 1fr 1fr 1fr;
        gap: 15px;
        margin-top: 30px;
    }
    &__item {
        display: flex;
        padding: 9px 15px 9px 5px;
        border: 1px solid #E0E0E0;
        border-radius: 8px;
        overflow: hidden;
        align-items: center;
        &-img {
            flex-shrink: 0;
            margin-right: 5px;
        }
        &-content {
            flex-grow: 1;
        }
        &-top {
            display: flex;
            align-items: center;
            justify-content: space-between;
        }
        &-title {
            font-weight: 500;
            font-size: 16px;
            line-height: 24px;
            text-transform: uppercase;
        }
        &-link {
            font-weight: 500;
            font-size: 14px;
            line-height: 21px;
            text-decoration-line: underline;
            color: $blue;
        }
        &-desc {
            font-weight: 400;
            font-size: 14px;
            line-height: 120%;
            margin-top: 5px;
            color: $grey;
        }
    }
}

.big-prize {
    margin-top: 33px;
    padding: 33px 0;
    &__items {
        margin-top: 30px;
        display: grid;
        grid-template-columns: 1fr 1fr 1fr;
        gap: 15px;
    }
}

.lottery {
    padding: 33px 0;
    &__items {
        margin-top: 15px;
        display: grid;
        grid-template-columns: repeat(5, 1fr);
        gap: 15px;
        padding: 0 35px;
    }
    &__link {
        display: block;
        margin-top: 15px;
        padding-right: 35px;
        text-align: right;
    }
}

.winners {
    padding: 33px 0;
    &__inner {
        display: flex;
    }
    &__left {
        flex-grow: 1;
    }
    &__right {
        width: 380px;
        flex-shrink: 0;
        margin-left: 15px;
    }
    &__badge {
        margin-bottom: 15px;
    }
    &__big {
        padding: 20px 20px 36px;
        border: 1px solid #E0E0E0;
        border-radius: 8px;
        &-top {
            display: flex;
            align-items: center;
            justify-content: center;
            img {
                margin-right: 20px;
            }
            &-title {
                font-weight: 500;
                font-size: 28px;
                line-height: 36px;
                text-transform: uppercase;
                strong {
                    color: $blue;
                    font-weight: 700;
                    font-size: 36px;
                    line-height: 22px;
                }
            }
        }
        &-wrap {
            margin-top: 25px;
            display: flex;
        }
        &-left {
            max-width: 415px;
            padding-bottom: 20px;
        }
        &-right {
            width: 232px;
            margin-left: 15px;
        }
        &-img {
            img {
                width: 100%;
            }
        }
        &-title {
            font-weight: 500;
            font-size: 16px;
            line-height: 150%;
        }
        &-desc {
            font-size: 14px;
            line-height: 150%;
            margin-top: 15px;
        }
        &-link {
            margin-top: 31px;
            display: block;
            text-align: center;
        }
        &-btn {
            margin-top: 34px;
            width: 100%;
        }
    }
    &__time {
        margin-top: 19px;
        display: flex;
        align-items: center;
        justify-content: center;
        &-title {
            font-weight: 500;
            font-size: 14px;
            line-height: 120%;
            margin-right: 10px;
        }
        &-nums {
            display: flex;
            align-items: center;
            position: relative;
            &:before {
                content: attr(data-date);
                position: absolute;
                z-index: 1;
                top: 100%;
                left: 13px;
                margin-top: 4px;
                font-size: 12px;
                line-height: 130%;
                margin-top: 5px;
            }
        }
        &-num {
            width: 20px;
            height: 20px;
            border-radius: 50%;
            display: flex;
            align-items: center;
            justify-content: center;
            color: #FFFFFF;
            font-weight: 500;
            font-size: 12px;
            background-color: $red;
            &:not(:last-child) {
                margin-right: 4px;
            }
        }
    }
    &__total {
        padding: 5px 10px 10px;
        border: 1px solid #E0E0E0;
        border-radius: 8px;
        text-align: center;
        &-title {
            font-size: 18px;
            line-height: 120%
        }
        &-sum {
            font-weight: 700;
            font-size: 26px;
            line-height: 22px;
            margin-top: 5px;
            color: $green;
        }
        &-bottom {
            margin-top: 5px;
            display: flex;
            align-items: center;
            justify-content: center;
        }
        &-num {
            font-weight: 500;
            font-size: 26px;
            line-height: 120%;
            margin-right: 10px;
        }
        &-ticket {
            font-size: 18px;
            line-height: 120%;
        }
    }
    &__items {
        margin-top: 30px;
    }
    &__item {
        &:not(:last-child) {
            margin-bottom: 15px;
        }
    }
}

.faq {
    padding: 33px 0;
    &__items {
        margin-top: 30px;
        max-width: 776px;
    }
    &__item {
        padding: 0 20px 10px;
        border-bottom: 1px solid #E0E0E0;
        &--active {
            .faq__item-btn {
                transform: rotate(180deg);
            }
        }
        &:not(:last-child) {
            margin-bottom: 18px;
        }
        &-show {
            display: flex;
            align-items: center;
            justify-content: space-between;
            cursor: pointer;
        }
        &-title {
            font-weight: 500;
            font-size: 18px;
            line-height: 24px;
            color: $blue;
        }
        &-hidden {
            margin-top: 20px;
            display: none;
        }
    }
}

@media screen and (max-width: 1200px) {
    .main {
        &__items {
            grid-template-columns: 1fr 1fr;
        }
    }
    .big-prize {
        &__items {
            grid-template-columns: 1fr 1fr;
        }
    }
    .lottery {
        &__items {
            grid-template-columns: repeat(4, 1fr);
        }
    }
    .winners {
        &__inner {
            display: block;
        }
        &__right {
            width: 100%;
            margin: 32px 0 0;
        }
    }
}

@media screen and (max-width: 992px) {
    .main {
        &__banner {
            width: calc(100% + 36px);
            margin-left: -18px;
            img {
                border-radius: 0;
            }
        }
        &__items {
            margin-top: 16px;
            grid-template-columns: 1fr;
            gap: 12px;
        }
        &__item {
            &-img {
                img {
                    width: 70px;
                }
            }
        }
    }
    .big-prize {
        margin-top: 15px;
        padding: 15px 0;
    }
    .lottery {
        padding: 15px 0;
        &__items {
            padding: 0;
            grid-template-columns: repeat(4, 1fr);
        }
        &__link {
            padding-right: 0;
            margin-top: 12px;
        }
    }
    .winners {
        padding: 15px 0;
    }
    .faq {
        padding: 15px 0;
    }
}

@media screen and (max-width: 900px) {
    .lottery {
        &__items {
            grid-template-columns: repeat(3, 1fr);
        }
    }
}

@media screen and (max-width: 768px) {
    .big-prize {
        &__items {
            grid-template-columns: 1fr;
            margin-top: 18px;
        }
    }
    .lottery {
        &__items {
            grid-template-columns: repeat(2, 1fr);
        }
    }
    .winners {
        &__big {
            padding: 16px;
            &-top {
                img {
                    width: 40px;
                }
                &-title {
                    font-size: 25px;
                    strong {
                        font-size: 25px;
                    }
                }
            }
            &-wrap {
                display: block;
            }
            &-left {
                max-width: 100%;
                width: 100%;
            }
            &-right {
                margin: 16px 0 0;
                width: 100%;
                text-align: center;
            }
            &-img {
                width: calc(100% + 32px);
                margin-left: -16px;
            }
            &-desc {
                margin-top: 8px;
            }
            &-link {
                margin-top: 12px;
                font-size: 14px;
            }
            &-btn {
                margin: 16px auto 0;
                width: 232px;
                max-width: 100%;
            }
        }
        &__time {
            margin-top: 16px;
        }
        &__items {
            margin-top: 16px;
        }
        &__item {
            &:not(:last-child) {
                margin-bottom: 8px;
            }
        }
    }
    .faq {
        &__item {
            padding-left: 0;
            padding-right: 0;
        }
    }
}

@media screen and (max-width: 370px) {
    .lottery {
        &__items {
            grid-template-columns: repeat(1, 1fr);
        }
    }
    .winners {
        &__big {
            &-top {
                img {
                    margin-right: 10px;
                }
                &-title {
                    font-size: 20px;
                    strong {
                        font-size: 20px;
                    }
                }
            }
        }
    }
}